import { FC, PropsWithChildren, forwardRef } from 'react';
import { TwBaseProps } from '../../types';
import {
  Select as MantineSelect,
  type SelectProps as MantineSelectProps,
  type DefaultProps,
  MantineTheme,
} from '@mantine/core';
import { styled } from '../../utils';

export interface SelectProps
  extends TwBaseProps,
    PropsWithChildren,
    Omit<MantineSelectProps, keyof Omit<DefaultProps, 'styles'>> {}

const Root = styled(MantineSelect)<MantineSelectProps>(() => ({}));

export const Select: FC<SelectProps> = forwardRef((props, ref) => {
  // TODO: Need to put in focus styles
  return (
    <Root
      {...props}
      styles={(theme: MantineTheme) => {
        return {
          input: {
            minHeight: '38px', // TODO: See if this should stay
            borderColor: theme.colors.gray[3],
          },
        };
      }}
      ref={ref}
    />
  );
});
